.ProxyConsole {
    text-align: left;
    width: 100%;
}

fieldset {
    font-size: 24px;
    text-align: left;
    width: calc(100% - 36px);
    margin: 0;
}

legend {
    font-size: 32px;
}

label {
    margin-left: 8px;
}

.ProxyUrl {
    font-size: 20px;
    width: calc(100% - 8px);
    padding: 4px;
}

.ProxyStartContainer {
    text-align: right;
}

.ProxyStartButton {
    background: #111;
    color: #f0f0f0;
    border: none;
    padding: 8px 16px;
    font-size: 20px;
    margin-bottom: 16px;
    border-radius: 4px;
    transition: all 0.15s;
}

.ProxyStartButton:hover {
    background: #222;
    color: #b0b0b0;
}

.ProxyStartButton:active {
    color: #222;
    background: #AAA;
}